<template>
  <scroll-view class="survey-list-wrapper" :data="surveyList" :pullup="pullup" @pullup="getMoreSurvey">
    <div class="scroll-content">
      <ul class="survey-list">
        <li class="item" v-for="item in surveyList" :key="item.id">
          <router-link :to="'/survey/'+item.id+'/detail'">
            <div class="cover">
              <img src="" alt="封面">
            </div>
            <div class="brief">
              <span class="title">{{item.title}}</span>
              <span class="author">{{item.author}}</span>
            </div>
            <div class="stamp">
              <span class="time">{{item.time}}</span>
              <span class="status" :status="item.status">{{item.status|status}}</span>
            </div>
          </router-link>
        </li>
      </ul>
      <loading-tip :tip="loadingTip" />
    </div>
  </scroll-view>
</template>

<script>
import service from '@/service';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  data() {
    return {
      surveyArg: { limit: 10, offset: 0, over: false }, // 获取调研的参数
      surveyList: [], // 调研列表
      pullup: true, // 上拉刷新
      loadingTip: '' // 加载提示
    };
  },
  created() {
    this.querySurvey();
  },
  computed: {},
  methods: {
    // 获取调研列表
    querySurvey() {
      if (this.querySurvey.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      service.survey
        .getList(this.surveyArg)
        .then(res => {
          if (res.data.code === 200) {
            this.surveyList.push(...res.data.data);
            if (!this.res.data.data.list.length && !this.querySurvey.offset) {
              this.loadingTip = '暂无数据';
              this.queryArg.over = true;
            } else if (this.surveyList.limit > this.surveyList.length) {
              this.loadingTip = '全部加载完毕';
              this.queryArg.over = true;
            }
          } else {
            console.error(res.data.msg);
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },
    // 上拉获取更多调研
    getMoreSurvey() {
      this.querySurvey.offset += this.querySurvey.limit;
      this.querySurvey();
    }
  },
  filters: {
    status(type) {
      switch (Number(type)) {
        case 1:
          return '已结束';
        case 2:
          return '已投票';
        case 3:
          return '暂停';
      }
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>
<style scoped lang="stylus">
@import "../../common/stylus/base"

.survey-list-wrapper
  position absolute
  width 100%
  height 100%
  background-color #efefef
  font-size 0
  .survey-list
    padding 0.27rem 0.24rem
    .item
      &>a
        background-color #fff
        display flex
        padding 0.1rem
        margin-bottom 0.2rem
        .cover
          margin-right 0.1rem
          img
            width 1rem
            height 1rem
            background-color #ccc
        .brief
          display flex
          flex-direction column
          justify-content space-between
          flex-grow 1
          .title
            overflow-x hidden
            white-space nowrap
            text-overflow ellipsis
            font-size 0.34rem
          .author
            font-size 0.32rem
            color #999
        .stamp
          display flex
          flex-direction column
          justify-content space-between
          .time
            font-size 0.32rem
            color #999
          .status
            font-size 0.32rem
            text-align right
            &[status="1"]
              color #2dc56d
            &[status="2"]
              color #d62525
            &[status="3"]
              color #f38534
            &[status="4"]
              color #2dc56d
            &[status="5"]
              color $color-main
</style>




