<template>
  <div class="news-detail">
    <!-- 新闻内容 -->
    <scroll-view class="scroll-wrapper" :data="comments" :data2="replysTemp" :pullup="scroll.pullup" @scrollToEnd="getMoreComments">
      <div class="scroll-content" @click="hideInput">
        <div class="detail-main">
          <p class="title">{{contentDetail.title}}</p>
          <div class="time-author">
            <span>{{contentDetail.timestamp}}</span>
            <span>{{contentDetail.author}}</span>
          </div>
          <div class="abstract">{{contentDetail.abstract}}</div>
          <div class="detail-content">
            <div v-html="contentDetail.content"></div>
            <ul class="attachment" v-if="contentDetail.type === '3'">
              <li>
                <a :href="contentDetail.filepath" :download="contentDetail.filename">
                  <span>{{contentDetail.filename}}</span>
                  <button>下载</button>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <!-- 评论 -->
        <div class="detail-comment">
          <ul class="divide-line">
            <li></li>
            <li>评论</li>
            <li></li>
          </ul>
          <div class="leave-comment" @click.stop>
            <a href="javascript:" class="edit" v-show="!isShow.commentForm && !userInfo.isAdmin" @click="showCommentForm">
              <img src="../assets/icon-edit.png" alt="icon">
              <span>评论</span>
            </a>
            <div class="form" v-show="isShow.commentForm">
              <form onsubmit="return false">
                <div class="input" contenteditable="true" @input="getCommentInput" ref="com_input"></div>
                <button type="submit" @click="sendComment">发送</button>
              </form>
            </div>
          </div>
          <ul class="comments">
            <li class="item" v-for="comment in comments" :key="comment.id">
              <div class="avatar">
                <img :src="comment.avatar" alt="头像">
              </div>
              <div class="text">
                <span class="nickname">{{comment.name === userInfo.name ? '我' : comment.name}}</span>
                <p class="message">{{comment.content}}</p>
                <span class="time">{{comment.timestamp}}</span>
                <a href="javascript:" class="reply-btn" v-show="userInfo.isAdmin && notSelf(comment)" @click.stop="showReplyForm(comment, comment.id, $event)">回复</a>
                <!-- 回复列表 -->
                <div class="reply-list" v-show="comment.reply_count || replys[comment.id]">
                  <ul>
                    <li v-for="(reply, index) in replys[comment.id] || []" :key="index">
                      <div class="avatar">
                        <img :src="reply.avatar" alt="头像">
                      </div>
                      <div class="text">
                        <span class="nickname">{{!(notSelf(reply)) ? '我' : reply.name}}</span>
                        <span class="txt-grey" v-show="reply.to_name">回复</span>
                        <span class="nickname">{{reply.to_name}}</span>
                        <p class="message">{{reply.content}}</p>
                        <span class="time">{{reply.timestamp}}</span>
                        <a href="javascript:" class="reply-btn" v-show="userInfo.isAdmin && notSelf(reply)" @click.stop="showReplyForm(reply, comment.id, $event)">回复</a>
                      </div>
                    </li>
                  </ul>
                  <a href="javascript:" v-show="showGetReplyBtn(comment)" @click="getReplys(comment.id)">{{formateGetReplyBtn(comment)}}</a>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <loading-tip :tip="loadingTip" />
      </div>
    </scroll-view>
    <transition name="fade">
      <toast-tip v-show="isShow.tip" :content="tips" />
    </transition>
    <form onsubmit="return false" class="form-reply" v-show="isShow.replyForm" @click.stop>
      <div class="input" :data-placeholder="ajax.reply.set.placeholder" @blur="hideInput" contenteditable="true" ref="reply_input" @input="getReplyInput"></div>
      <button type="submit" @click="sendReply">确定</button>
    </form>
  </div>
</template>

<script>
// import service from '@/service';
import axios from 'axios';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
import ToastTip from '@/components/tips/ToastTip';
export default {
  name: 'NewsNoticeDetail',
  props: {
    ajax: {
      // 获取详情
      detail: {
        method: { type: String, default: 'post' },
        urls: [Array],
        flag: [Number], // 0、预览，1、正式
        args: {
          id: [String, Number] // 详情id
        }
      },
      // 评论
      comments: {
        // 获取评论
        get: {
          method: { type: String, default: 'post' },
          url: [String],
          args: {
            id: [String, Number], // 详情id
            offset: [Number],
            limit: [Number],
            type: [String, Number] // 通知类别，新闻没有
          }
        },
        // 评论
        set: {
          method: { type: String, default: 'post' },
          url: '',
          args: {
            news_id: [String, Number],
            content: [String],
            type: [Number]
          }
        }
      },
      // 回复
      reply: {
        // 获取回复
        get: {
          method: { type: String, default: 'post' },
          url: [String],
          args: {
            // comment_id为key的集合
            // [comment_id][id]
            // [comment_id][offset]
            // [comment_id][limit]
          }
        },
        // 回复
        set: {
          method: { type: String, default: 'post' },
          url: 'dfdf',
          placeholder: '回复',
          args: {
            news_id: [String, Number],
            notice_id: [String, Number],
            comment_id: [String, Number],
            content: [String],
            type: [String, Number], // 通知类别，新闻没有
            to_memid: [String, Number], // 发送给成员
            to_adminid: [String, Number] // 发送给管理员
          }
        }
      }
    }
  },
  data() {
    return {
      // 用户信息
      userInfo: {},
      // 详情数据
      contentDetail: {},
      // 评论数据
      comments: [],
      // 回复集合
      replys: {},
      // scroll
      scroll: {
        pullup: true
      },
      // 显示隐藏
      isShow: {
        commentForm: false,
        replyForm: false,
        tip: false
      },
      // 发送时保存的临时数据
      temp: {
        comment: {
          id: 0, // 服务器返回
          memid: 0, // 获取详情时获得
          content: '', // 获取输入时取得
          timestamp: '刚刚',
          name: '', // 获取详情时获得
          avatar: '', // 获取详情时获得
          reply_count: 0 // 默认0
        },
        reply: {
          admin_id: 0,
          memid: 0, // 获取详情时获得
          content: '', // 获取输入时取得
          timestamp: '刚刚',
          name: '', // 获取详情时获得
          avatar: '', // 获取详情时获得
          to_name: '' // 显示输入框时取得
        }
      },
      // 仅用于更新scroll
      replysTemp: [],
      // 加载提示
      loadingTip: '',
      tips: ''
    };
  },
  created() {
    this.getDetail();
    this.getComments();
  },
  methods: {
    // 获取详情
    getDetail() {
      let opt = this.ajax.detail;
      axios[opt.method](opt.urls[opt.flag], opt.args)
        .then(res => {
          if (res.data.code === 200) {
            let info = res.data.data.detail;
            this.contentDetail = info;
            // 保存当前用户信息
            Object.assign(this.userInfo, {
              name: info.name,
              avatar: info.avatar,
              isAdmin: Number(info.isAdmin),
              memid: Number(info.memid),
              adminId: Number(info.admin_id),
              memberId: Number(info.memid)
            });
            // save temp data
            Object.assign(this.temp.comment, {
              name: info.name,
              avatar: info.avatar,
              memid: info.memid
            });
            Object.assign(this.temp.reply, {
              name: info.name,
              avatar: info.avatar,
              memid: info.memid,
              admin_id: info.admin_id
            });
          } else {
            this.showTips(res.data.msg);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    //  获取评论
    getComments() {
      let opt = this.ajax.comments.get;
      if (opt.over) {
        return;
      }
      this.loadingTip = '正在加载...';
      axios[opt.method](opt.url, opt.args)
        .then(res => {
          if (res.data.code === 200) {
            let list = res.data.data.list;
            this.comments.push(...list);
            if (!opt.args.offset && !list.length) {
              this.loadingTip = '暂无数据';
              opt.over = true;
            } else if (list.length < opt.args.limit) {
              this.loadingTip = '全部加载完毕';
              opt.over = true;
            } else {
              this.loadingTip = '释放加载更多';
            }
          } else {
            this.loadingTip = '加载失败';
            this.showTips(res.data.msg);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取更多评论
    getMoreComments() {
      this.ajax.comments.get.args.offset += this.ajax.comments.get.args.limit;
      this.getComments();
    },
    // 显示评论框
    showCommentForm(event) {
      if (event._constructed) {
        this.isShow.commentForm = true;
        setTimeout(
          function() {
            this.$refs.com_input.focus();
          }.bind(this),
          100
        );
      }
    },
    // 获取评论输入
    getCommentInput() {
      clearTimeout(this.timeHandle);
      this.timeHandle = setTimeout(
        function() {
          this.ajax.comments.set.args.content = this.$refs.com_input.innerText.trim();
          this.temp.comment.content = this.ajax.comments.set.args.content;
        }.bind(this),
        200
      );
    },
    // 发送评论
    sendComment(text) {
      let opt = this.ajax.comments.set;
      if (opt.args.content.length && event._constructed) {
        axios[opt.method](opt.url, opt.args)
          .then(res => {
            if (res.data.code === 200) {
              this.isShow.commentForm = false;
              this.$refs.com_input.innerText = '';
              this.showTips('评论已发送');
              // save temp data
              this.temp.comment.id = res.data.data.id;
              let json = JSON.parse(JSON.stringify(this.temp.comment)); // 深拷贝
              res.data.data.id && this.comments.unshift(json);
            }
          })
          .catch(err => {
            console.error(err);
          });
      } else {
        this.showTips('评论内容不能为空');
      }
    },
    // 显示回复框
    showReplyForm(comment, commentId, $event) {
      if (event._constructed) {
        let opt = this.ajax.reply.set;
        this.isShow.replyForm = true;
        // 当前用户是管理员且被评论的对象有memid(非管理员)则to_memid否则to_adminid
        this.userInfo.isAdmin && Number(comment.memid)
          ? (opt.args.to_memid = comment.memid)
          : (opt.args.to_adminid = comment.admin_id);
        opt.args.comment_id = commentId;
        opt.placeholder = '回复 ' + comment.name;
        this.temp.reply.to_name = comment.name;
        setTimeout(
          function() {
            this.$refs.reply_input.focus();
          }.bind(this),
          100
        );
      }
    },
    // 获取回复输入
    getReplyInput() {
      clearTimeout(this.timeHandle);
      this.timeHandle = setTimeout(
        function() {
          this.ajax.reply.set.args.content = this.$refs.reply_input.innerText.trim();
          this.temp.reply.content = this.ajax.reply.set.args.content;
        }.bind(this),
        200
      );
    },
    // 发送回复
    sendReply() {
      let opt = this.ajax.reply.set;
      axios[opt.method](opt.url, opt.args)
        .then(res => {
          if (res.data.code === 200) {
            this.isShow.replyForm = false;
            this.$refs.reply_input.innerText = '';
            this.showTips('回复已发送');
            // push reply
            if (this.replys[opt.args.comment_id]) {
              // 深拷贝
              let json = JSON.parse(JSON.stringify(this.temp.reply));
              this.replys[opt.args.comment_id].unshift(json);
            } else {
              this.$set(this.replys, opt.args.comment_id, [this.temp.reply]);
              this.replys[opt.args.comment_id].tip = '加载更多评论';
            }
            // offset +1
            let getOpt = this.ajax.reply.get.args[opt.args.comment_id];
            if (getOpt) {
              getOpt.offset++;
            } else {
              this.ajax.reply.get.args[opt.args.comment_id] = { id: opt.args.comment_id, limit: 5, offset: 1 };
            }
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取回复
    getReplys(id) {
      let opt = this.ajax.reply.get;
      if (opt.args[id]) {
        if (opt.args[id].over) {
          return;
        }
      } else {
        opt.args[id] = { id: id, offset: 0, limit: 5 };
      }
      axios[opt.method](opt.url, opt.args[id])
        .then(res => {
          if (res.data.code === 200) {
            const list = res.data.data.list;
            this.replysTemp.push(list); // 仅用于更新scroll
            if (this.replys[id]) {
              this.replys[id].push(...list);
            } else {
              this.$set(this.replys, id, list);
            }
            if (list.length === opt.args[id].limit) {
              this.replys[id].tip = '加载更多评论';
              opt.args[id].offset += opt.args[id].limit;
            } else {
              this.replys[id].over = true;
            }
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 隐藏输入框
    hideInput() {
      if (!this.$refs.com_input.innerText.trim()) {
        this.isShow.commentForm = false;
      }
      if (!this.$refs.reply_input.innerText.trim()) {
        this.isShow.replyForm = false;
      }
    },
    // 显示'加载回复'按钮
    showGetReplyBtn(comment) {
      return (
        !(this.replys[comment.id] && this.replys[comment.id].over) &&
        Number(comment.reply_count)
      );
    },
    // 格式化加载回复的按钮
    formateGetReplyBtn(comment) {
      return (
        (this.replys[comment.id] && this.replys[comment.id].tip) ||
        '共 ' + comment.reply_count + ' 条回复'
      );
    },
    // 非己
    notSelf(item) {
      if (this.userInfo.isAdmin) {
        return Number(item.admin_id) !== this.userInfo.adminId;
      } else {
        return Number(item.memid) !== this.userInfo.memberId;
      }
    },
    // 显示提示
    showTips(txt) {
      this.tips = txt;
      this.isShow.tip = true;
      setTimeout(
        function() {
          this.isShow.tip = false;
        }.bind(this),
        1500
      );
    }
  },
  components: {
    ScrollView,
    LoadingTip,
    ToastTip
  }
};
</script>

<style lang="stylus" scoped>
@import "../common/stylus/base"

/* 动画 */
.fade-enter-active, .fade-leave-active
  transition opacity 0.6s
.fade-enter, .fade-leave-to
  opacity 0
.news-detail
  position absolute
  top 0
  left 0
  bottom 0
  right 0
  font-size 0
  /* 新闻 */
  .scroll-wrapper
    position absolute
    width 100%
    height 100%
    overflow hidden
    .scroll-content
      padding-bottom 0.1rem
      .detail-main
        padding 0.2rem
        margin-bottom 0.3rem
        background-color #fff
        box-shadow 0 2px 8px 1px #ddd
        .title
          margin-bottom 0.2rem
          font-size 0.48rem
        .time-author
          font-size 0.34rem
          color #777
          span
            margin-right 0.5rem
        .detail-content
          padding 0.3rem
          font-size 0.38rem
          word-break break-all
          .attachment>li>a
            display flex
            justify-content space-between
            button
              border 0.01rem solid $color-main
              border-radius 0.08rem
              background-color #fff
              color $color-main
        .abstract
          font-size 0.36rem
          color #666
      /* 评论 */
      .detail-comment
        padding 0 0.4rem
        .divide-line
          display flex
          margin-bottom 0.2rem
          li
            flex-shrink 1
            align-self center
            font-size 0.36rem
            &:nth-child(2)
              margin 0 0.2rem
            &:first-child, &:last-child
              flex-grow 1
              border-bottom 0.01rem solid #ccc
        .leave-comment
          text-align right
          .edit
            display inline-block
            img
              width 0.36rem
              height 0.4rem
              margin-right 0.08rem
            span
              font-size 0.38rem
          form
            display flex
            margin 0 -0.4rem
            padding 0.12rem
            background-color #fff
            .input
              flex 1
              min-height 0.5rem
              border-bottom 1px solid $color-main
              margin 0 0.15rem
              padding 0.12rem 0
              outline none
              font-size 0.36rem
              text-align justify
              caret-color $color-main
            button
              width 1rem
              height 0.55rem
              border none
              border-radius 0.08rem
              align-self flex-end
              background-color $color-main
              color #fff
        .comments
          .item
            display flex
            margin-bottom 0.3rem
            .avatar
              flex-shrink 0
              img
                width 0.88rem
                height 0.88rem
                background-color #ccc
            .text
              flex-grow 1
              margin-left 0.33rem
              .nickname
                margin-bottom 0.1rem
                font-size 0.34rem
              .txt-grey
                padding 0 0.05rem
                font-size 0.34rem
                color $color-txt-grey
              .message
                margin-top 0.1rem
                margin-bottom 0.1rem
                text-align justify
                font-size 0.38rem
                color #444
              .time
                font-size 0.34rem
                color #999
              .reply-btn
                margin-left 0.3rem
                font-size 0.34rem
                color lighten($color-link, 30%)
              .reply-list
                margin-top 0.1rem
                &>ul>li
                  display flex
                  margin-bottom 0.12rem
                  .avatar
                    flex-shrink 0
                  .text
                    flex-grow 1
                    margin-bottom 0.2rem
                a
                  font-size 0.34rem
                  color lighten($color-link, 30%)
  .form-reply
    display flex
    position absolute
    bottom 0
    width 100%
    padding-right 0.12rem
    background-color #fff
    .input
      box-shadow 0 -0.01rem 0.08rem 0.02rem #eee
      flex-grow 1
      padding 0.12rem 0.08rem
      border-bottom 0.03rem solid $color-main
      margin 0 0.15rem
      text-align justify
      font-size 0.36rem
      outline none
      &:empty
        &::before
          display inline
      &::before
        display none
        content attr(data-placeholder)
        padding-left 0.1rem
        font-size 0.34rem
        color $color-txt-grey
    button
      align-self center
      width 1rem
      height 0.55rem
      border none
      border-radius 0.05rem
      background-color $color-main
      color #fff
</style>




