<template>
  <notice-list :ajaxParam="ajaxParam" :pageName="'notice'" />
</template>

<script>
import service from '@/service';
import NoticeList from '@/components/NewsNoticeList';
export default {
  data() {
    return {
      ajaxParam: {
        method: 'post',
        url: service.notice.getList(),
        opt: {
          limit: 10,
          offset: 0
        }
      }
    };
  },
  components: {
    NoticeList
  }
};
</script>
<style lang="stylus" scoped>
</style>




