import axios from 'axios';
const HOST = '';
const SUFFIX = '';
export default {
  // 公司简介
  company: {
    getContent(opt) {
      return axios.post(HOST + '/weixin/profile/index' + SUFFIX, opt);
    }
  },
  // 产品
  product: {
    // 产品名称
    getName(opt) {
      return axios.post(HOST + '/weixin/product/lists' + SUFFIX, opt);
    },
    // 产品列表
    getList(opt) {
      return axios.post(HOST + '/weixin/product/investLists' + SUFFIX, opt);
    },
    // 产品详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/product/detail' + SUFFIX, opt);
    },
    // 报告详情
    getReport(opt) {
      return axios.post('xxxx', opt);
    },
    // 历史报告
    getHistoryReport(opt) {
      return axios.post(HOST + '/weixin/product/reportLists' + SUFFIX, opt);
    }
  },
  // 新闻
  news: {
    // 新闻列表
    getList() {
      return HOST + '/weixin/news/lists' + SUFFIX;
    },
    // 新闻详情(0、预览；1、正式)
    getDetail() {
      return [
        HOST + '/weixin/news/previewDetail' + SUFFIX,
        HOST + '/weixin/news/detail' + SUFFIX
      ];
    },
    // 发送评论
    sendMsg(opt) {
      return HOST + '/weixin/news/comment' + SUFFIX;
    },
    // 回复评论
    replyComment(opt) {
      return HOST + '/weixin/news/reply' + SUFFIX;
    },
    // 获取回复内容
    getReply(opt) {
      return HOST + '/weixin/news/readReply' + SUFFIX;
    },
    // 获取评论
    getComments(opt) {
      return HOST + '/weixin/news/readComment' + SUFFIX;
    }
  },
  // 通知
  notice: {
    // 通知列表
    getList() {
      return HOST + '/weixin/notice/lists' + SUFFIX;
    },
    // 通知详情(0、预览；1、正式)
    getDetail() {
      return [
        HOST + '/weixin/notice/previewDetail' + SUFFIX,
        HOST + '/weixin/notice/detail' + SUFFIX
      ];
    },
    // 发送评论
    sendMsg() {
      return HOST + '/weixin/notice/comment' + SUFFIX;
    },
    // 回复评论
    replyComment() {
      return HOST + '/weixin/notice/reply' + SUFFIX;
    },
    // 获取回复内容
    getReply() {
      return HOST + '/weixin/notice/readReply' + SUFFIX;
    },
    // 获取评论
    getComments() {
      return HOST + '/weixin/notice/readComment' + SUFFIX;
    }
  },
  // 投票
  vote: {},
  // 调研
  survey: {
    // 调研列表
    getList(opt) {
      return axios.post(HOST + '/weixin/survey/list' + SUFFIX, opt);
    },
    // 调研详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/survey/detail' + SUFFIX, opt);
    }
  }
};



