<template>
  <detail-component :ajax="ajax" />
</template>

<script>
import service from '@/service';
import DetailComponent from '@/components/NewsNoticeDetail';
export default {
  name: 'NewsDetail',
  data() {
    return {
      ajax: {
        // 获取详情
        detail: {
          method: 'post',
          urls: service.news.getDetail(),
          flag: this.$route.params.flag, // 0、预览，1、正式
          args: {
            id: this.$route.params.id // 详情id
          }
        },
        // 评论
        comments: {
          // 获取评论
          get: {
            method: 'post',
            url: service.news.getComments(),
            args: {
              id: this.$route.params.id, // 详情id
              offset: 0,
              limit: 8,
              type: this.$route.params.type // 通知类别，新闻没有
            }
          },
          // 评论
          set: {
            method: 'post',
            url: service.news.sendMsg(),
            args: {
              news_id: this.$route.params.id,
              content: '',
              type: this.$route.params.type
            }
          }
        },
        // 回复
        reply: {
          // 获取回复
          get: {
            method: 'post',
            url: service.news.getReply(),
            args: {
              // comment_id为key的集合
              // id: [String, Number], // 评论id
              // offset: [Number],
              // limit: [Number]
            }
          },
          // 回复
          set: {
            method: 'post',
            url: service.news.replyComment(),
            placeholder: '回复',
            args: {
              news_id: this.$route.params.id,
              comment_id: 0,
              content: '',
              type: this.$route.params.type // 通知类别，新闻没有
              // to_memid: 0, // 发送给成员
              // to_adminid: 0 // 发送给管理员
            }
          }
        }
      }
    };
  },
  components: {
    DetailComponent
  }
};
</script>

<style lang="stylus" scoped>
</style>




