<template>
  <scroll-view class="scroll-wrapper" :data="itemsList" :pullup="pullup" @scrollToEnd="pullUpGetMore">
    <div class="scroll-content">
      <ul class="items-list">
        <li class="item" v-for="item in itemsList" :key="item.id">
          <router-link :to="linkToDetail(item.id, item.type)">
            <div class="text">
              <p class="title">{{item.title}}</p>
              <span class="time">{{item.timestamp}}</span>
            </div>
            <div class="cover" v-show="item.imgurl">
              <img :src="item.imgurl" alt="cover">
            </div>
          </router-link>
        </li>
      </ul>
      <loading-tip :tip="loadingTip"></loading-tip>
    </div>
  </scroll-view>
</template>
<script>
// import service from '@/service';
import axios from 'axios';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  props: {
    // 页面名称（新闻|通知）
    pageName: {
      type: String,
      default: 'news',
      required: true
    },
    // 列表ajax查询参数
    ajaxParam: {
      method: {
        type: String,
        default: 'post',
        required: true
      },
      url: String,
      opt: {
        // 只有新闻有这个字段
        type: {
          type: Number
        },
        limit: {
          type: Number,
          default: 10,
          required: true
        },
        offset: {
          type: Number,
          default: 0,
          required: true
        }
      }
    }
  },
  data() {
    return {
      suffix: '/aid/' + this.$route.params.aid, // 用户信息后缀
      itemsList: [], // 列表数组
      pullup: true, // scroll-view允许上拉加载更多,
      loadingTip: '' // 加载提示
    };
  },
  created() {
    // 获取数据
    this.getItemsList();
  },
  methods: {
    // 服务器获取新列表数据
    getItemsList() {
      if (this.ajaxParam.opt.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      axios[this.ajaxParam.method.toLowerCase()](
        this.ajaxParam.url,
        this.ajaxParam.opt
      )
        .then(res => {
          if (res.data.code === 200) {
            this.itemsList.push(...res.data.data.list);
            if (!this.ajaxParam.opt.offset && !res.data.data.list.length) {
              this.loadingTip = '暂无数据';
              this.ajaxParam.opt.over = true;
            } else if (res.data.data.list.length < this.ajaxParam.opt.limit) {
              this.loadingTip = '全部加载完毕';
              this.ajaxParam.opt.over = true;
            } else {
              this.loadingTip = '释放加载更多';
            }
          } else {
            console.error(res.data.msg);
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },
    // 返回进入详情页的链接
    linkToDetail(id, type) {
      switch (this.pageName) {
        case 'news':
          return '/news/' + id + '/detail/1' + this.suffix;
        case 'notice':
          return '/notice/' + id + '/detail/1/' + type + this.suffix;
      }
    },
    // 上拉加载更多
    pullUpGetMore() {
      this.ajaxParam.opt.offset += this.ajaxParam.opt.limit;
      this.getItemsList();
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>

<style lang="stylus" scoped>
@import "../common/stylus/base"

.scroll-wrapper
  position absolute
  width 100%
  height 100%
  background-color #efefef
  overflow hidden
  font-size 0
  .scroll-content
    .items-list
      padding 0.3rem
      background-color #fff
      /* 列表项 */
      .item
        border-bottom 0.01rem solid #ccc
        padding 0.2rem 0
        &>a
          display flex
          justify-content space-between
          .text
            .title
              margin-bottom 0.2rem
              word-break break-all
              font-size 0.34rem
              color #000
            .time
              font-size 0.3rem
              color #777
          .cover
            flex-shrink 0
            img
              width 1rem
              height 1rem
              background-color #ccc
</style>



