<template>
  <news-list :ajaxParam="ajaxParam" :pageName="'news'" />
</template>

<script>
import service from '@/service';
import NewsList from '@/components/NewsNoticeList';
export default {
  data() {
    return {
      ajaxParam: {
        method: 'post',
        url: service.news.getList(),
        opt: {
          type: 1,
          limit: 10,
          offset: 0
        }
      }
    };
  },
  components: {
    NewsList
  }
};
</script>

<style lang="stylus" scoped>

</style>



